import { Component, Fragment, Prop, State, h } from '@stencil/core';
// import { isBrowserEnv } from 'src/utils/browser'
import WebJsSdkService from 'src/utils/jssdk';
import arrow from '../../../assets/images/ico-arrow.svg';

@Component({
  tag: 'widget-header-more-button',
})
export class MoreButton {
  constructor() {
    this.onClick = this.onClick.bind(this);
  }

  private webJsSdkService = new WebJsSdkService();

  @State() message: string = '';
  @State() userAgent: string = '';
  @State() isWebview: boolean;

  /**
   * Url
   */
  @Prop() url?: string = '';

  async onClick(e?: MouseEvent) {
    const isWebview = await this.webJsSdkService.isWebview();
    this.isWebview = isWebview;

    try {
      e.preventDefault();
      e.stopPropagation();

      if (isWebview) {
        await this.webJsSdkService.openNativeInAppBrowser(this.url);
        return;
      }

      this.message = 'isWebview: false';
      window.open(this.url, '_blank');
    } catch (error) {
      const userAgent = window.navigator.userAgent;

      this.userAgent = userAgent;
      this.message = JSON.stringify(error);
    }
  }

  render() {
    return (
      <Fragment>
        <button
          onClick={this.onClick}
          style={{
            display: 'inline-flex',
            flexWrap: 'nowrap',
            alignItems: 'center',
            textDecoration: 'none',
            padding: '12px',
            fontSize: '16px',
            color: '#0033E8',
            background: 'white',
            border: 'none',
            cursor: 'pointer',
          }}
        >
          <sapn>查看更多</sapn>
          <img src={arrow} />
        </button>
        <div style={{ background: 'white', color: 'black', height: '40px', display: 'flex', flexDirection: 'column' }}>
          <div>isWebview: {this.isWebview}</div>
          <div>userAgent: {this.userAgent}</div>
          <div>err: {this.message}</div>
        </div>
      </Fragment>
    );
  }
}
